	import paging from '@/components/yang-paging/fy.vue'
export default {
	components:{
		paging
	},
	data() {
		return {
			userlist:'',//user用户
			sorttid:1,
			sorttid2:0,
			disabled:false,
			zumen: [
				{
					name: '查询全部代理',
					id: 1,
					sortt:0,
					http:'/pages/index/userlist'
				},
				{
					name: '查询审核订单',
					id: 2,
					sortt:1,
					http:'/pages/index/index'
				},
				{
					name: '查询所有订单',
					id: 3,
					sortt:2,
					http:'/pages/index/index'
				},
				{
					name: '查询所有云进订单',
					id: 4,
					sortt:3,
					http:'/pages/index/yunOrder'
				},
				{
					name: '查询所有云提货单',
					id: 6,
					sortt:3,
					http:'/pages/index/yunOrderdelivery'
				},
				{
					name: '查询信息反馈',
					id: 5,
					sortt:4,
					http:'/pages/index/userfeedback'
				}
			],
			stourname:[
				{name:'全部',id:0},
				{name:'总代理',id:1},
				{name:'省代理',id:2},
				{name:'市代理',id:3}
			],
			menuLists:[],
			querySearch:null,//用户输入内容
			navigatepageNums:null,
			pageNum:1,//第几页
			pages:0,//总页数
			pageSize:0,//每页数
			total:0,//数据总条数
			confirmuser:null
		};
	},
	onLoad() {
		this.userlistdata();
	},
	onShow() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		if (this.userlist=='') {
			this.utils.error('账号未登录！', () => {
				// this.doUrl('/pages/index/index');
				// this.utils.navback();
				this.doUrl('/pages/user/login');
			});
		}
	},
	methods: {

		//退出登录
		loginout(){
			uni.removeStorageSync('userlist');
			this.userlist = '';
			this.utils.success('退出成功！', () => {
				this.doUrl('/pages/user/login');
				// this.utils.navback();
			});
		},
		userquery(){
			if (this.querySearch != null) {
				this.sorttid2 = 0;
				this.utils.showloading();
				this.http.getApi('/user/SelectByphone',{status:this.querySearch}, 'get', this.userlist.storeOpendid).then(res => {
					uni.hideLoading();
					console.log(res);
					// this.confirmuser = null;
					this.menuLists = [res.data];
					this.utils.success('查询成功！', () => {
					});
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
					this.utils.success(err.msg, () => {
						// this.doUrl('/pages/user/login');
						// this.utils.navback();
					});
				});
			} else{
				this.utils.alert('请输入查询的参数',()=>{
					return;
				});
			}
		},
		sorttid2ck(item) {
			//点击其中一个选项
			if (this.sorttid2 == item) {
				return false;
			} else {
				this.sorttid2 = item;
				this.pageNum = 1;
				// this.judgeOrder(this.status);
			}
		},
		modifyuser(item){
			this.confirmuser = item;
				console.log(this.confirmuser);
				this.utils.error('请在顶部输入框内输入需要修改的参数！', () => {
					
				});
			
		},
		confirmusere(){
			console.log(this.confirmuser);
			this.utils.showloading();
			this.http.getApi('/user/updateUser',this.confirmuser, 'post', this.userlist.storeOpendid).then(res => {
				uni.hideLoading();
				console.log(res);
				this.confirmuser = null;
				this.utils.success('修改成功！', () => {
					// this.doUrl('/pages/user/login');
					// this.utils.navback();
				});
			})
			.catch(err => {
				console.log(err);
				uni.hideLoading();
			});
		},
		//页码渲染
		add(index) {
			// console.log(index);
			this.pageNum = index;
			this.userlistdata(this.pageNum);
		},
		
		userlistdata(){
			this.utils.showloading();
			this.http.getApi('/user/getAllUserNo', {pageNum:this.pageNum, pageSize:10}, 'get', this.userlist.storeOpendid).then(res => {
				uni.hideLoading();
				if (res.data.list.length==0) {
					this.menuLists = null;
				} else{
					// console.log('------------------------------');
					// console.log(this.mensname.id);
					console.log('------------------------------');
					console.log(res);
					this.menuLists = res.data.list;
					this.pageNum = res.data.pageNum;
					this.navigatepageNums = res.data.navigatepageNums;
					this.total = res.data.total;
					this.pageSize = res.data.pageSize;
				}
			})
			.catch(err => {
				console.log(err);
				uni.hideLoading();
			});
		}
	}
};
